html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background-color: #96a5cc;
  background-image: linear-gradient(90deg, #e2e6f1 30%, #ffffff 100%);
  color: black;
  font-family: "Source Sans Pro", sans-serif; }

.header {
  grid-area: header; }
  .header__title {
    color: #00487c;
    margin-bottom: 16px; }

.site-header {
  background: #000000;
  box-sizing: border-box;
  left: 0;
  padding: 8px;
  position: fixed;
  width: 100%;
  top: 0; }
  @media (max-width: 640px) {
    .site-header {
      min-height: 48px; } }

.navigation__list {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center; }
  @media (max-width: 640px) {
    .navigation__list {
      flex-direction: column;
      display: none; }
      .navigation__list--visible {
        display: flex; } }

.navigation__item {
  display: block;
  margin: 0 16px; }
  @media (max-width: 640px) {
    .navigation__item {
      margin: 16px; } }

.navigation__link {
  color: #e2e6f1;
  text-decoration: none; }
  .navigation__link:hover {
    color: #E59400;
    border-bottom: #E59400 solid 2px; }

@media (min-width: 640px) {
  .navigation__switcher {
    display: none; } }

@media (max-width: 640px) {
  .navigation__switcher {
    border: none;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px;
    color: #ffffff; } }

.main {
  grid-area: content;
  display: grid;
  grid-template-areas: "header header" "about diary";
  grid-column: 1fr 1fr;
  grid-gap: 16px;
  max-width: 700px;
  margin: 64px auto; }
  @media (min-width: 640px) {
    .main {
      max-width: 900px; } }
  @media (max-width: 640px) {
    .main {
      grid-template-areas: "header header" "about diary";
      grid-column: 1fr; } }

.about__header {
  color: #1460aa; }

.about__link {
  color: #1a936f;
  text-decoration: none; }
  .about__link:hover {
    color: #00487c;
    border-bottom: #E59400 solid 2px; }

.about__picture {
  width: 100%;
  max-width: 100px;
  display: block;
  float: left;
  margin: 0 16px 16px 0; }
  .about__picture:hover {
    transform: scale(2.2); }

.about__paragraph {
  padding: 0;
  line-height: 24px; }
  .about__paragraph--mini {
    font-size: 10px; }

.diary {
  grid-area: diary;
  display: grid;
  grid-template-areas: "headerDay headerDay" "day day";
  grid-column: 1fr 1fr; }
  .diary__header {
    grid-area: headerDay;
    text-align: center;
    color: #1460aa; }
  @media (max-width: 640px) {
    .diary {
      grid-template-areas: "headerDay" "day";
      grid-column: 1fr; } }

.day {
  padding: 16px; }
  .day__header {
    color: #0496ff;
    margin: 0 0 8px 0;
    font-size: 16px; }
    .day__header--weekend {
      color: #f14545; }
  .day__link {
    color: #1a936f;
    text-decoration: none; }
    .day__link:hover {
      color: #00487c;
      border-bottom: #E59400 solid 2px; }
  .day__paragraph {
    padding: 0;
    margin: 0;
    line-height: 24px; }

.article__footer {
  padding: 24px;
  font-style: italic;
  color: #3c359b; }

.article__paragraph {
  padding: 0; }

.article__picture {
  width: 100%;
  max-width: 200px;
  display: block; }
  .article__picture--left {
    float: left;
    margin: 0 16px 16px 0; }
  .article__picture--right {
    float: right;
    margin: 0 0 0 16px; }

.article__title {
  color: #00487c;
  padding: 24px 0 0 0; }

.footer {
  position: fixed;
  width: 100%;
  padding: 10px;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  background-color: #666;
  color: #ffffff; }
